<template>
    <div class="detail" >
      <div v-for="item in detail" :key="item.id">
      <img :src='item.img'>
      <p class="pra">{{item.pra}}</p>
      </div>
      </div>
</template>

<script>
import { getDetail } from '@/service'
export default {
  name: 'wenda',
  data () {
    return {
      detail: []
    }
  },
  mounted () {
    getDetail().then(resp => {
      this.detail = resp.data.data
    })
  }

}
</script>

<style lang="scss" scoped>
.detail{
  .pra{
    text-indent: 2em;
    font-size: 15px;
  }
  img{
    width: 100%
  }
}
</style>



